import React, { useState } from 'react';

const AutoResizeTextarea = ({rows, value, ...props}) => {


  const calculateRows = () => {
    // Count the number of lines in the textarea value
    const lineCount = (value.match(/\n/g) || []).length + 1;
    // Set a minimum number_ of rows to ensure visibility
    const minRows = rows;
    const maxRows = 6; // Set your desired maximum number of rows

    // Return a value within the specified range
    return Math.min(Math.max(lineCount, minRows), maxRows);
  };

  return (
    <textarea
      rows={calculateRows()}
      style={{ resize: 'none' }}
      value={value}
      {...props}
    />
  );
};

export default AutoResizeTextarea;