import React, { useContext } from 'react'
import { hideUnhide } from '../../../../utils'
import { BrowserContext } from '../../../../store/BrowserContext'
import TimeElapsed from '../../../../Components/TimeElapsed'
import {TranslationContext} from '../../../../store/TranslationContext'

const UserMessage = ({message, lastMessageId}) => {
  const {togglePopup,Right} = useContext(BrowserContext)
  const {language} = useContext(BrowserContext).browserData
  const state = message.state || 'sent'

  const buttonClickHandler=()=>{
    hideUnhide(`message-time-${message.id}`, togglePopup)
    if (lastMessageId == message.id) setTimeout(()=>{document.getElementById('chatbox').scrollTop = 0}, 0)
  }
  const {translate} = useContext(TranslationContext)
  return (
    <div className={`${ language == 'ar' ? 'd-ltr' : 'd-rtl' }`}>
          <div className={`d-flex align-items-end`}>
            <span className='position-relative'> 
              <i hidden={state == 'sent'}  className={`${state === 'notSent' ? 'fa-solid fa-triangle-exclamation text-danger' : 'fa-regular fa-paper-plane color-theme'} fa-xs`}></i>
            </span>
            <button onClick={buttonClickHandler} className={`py-2 px-3 w-fit-content m-1 border rounded-4 ${state === 'notSent' ? 'border-danger text-danger' : 'border-success color-theme'} small text-start`} style={{maxWidth: '70%'}}>{message.text}</button>
          </div>
          { message.sent_at === 'Sending' ? 
            <span id={`message-time-${message.id}`} hidden className='px-2 small color-theme'>{translate('Sending')}</span> : 
            message.sent_at === 'Not sent' ?
            <span id={`message-time-${message.id}`} hidden className='px-2 small text-danger'>{translate('Not sent')}</span>:
            <TimeElapsed language={language} time={message.sent_at} hidden id={`message-time-${message.id}`} className='px-2 small text-grey'/>
          }
    </div>
  )
}

export default UserMessage