import React, { useContext, useEffect, useRef, useState } from 'react'
import { Avatar } from '../../StyledComponents'
import AutoResizeTextarea from '../../Components/UI/AutoResizeTextarea'
import axios from 'axios'
import { Link, useLocation, useParams } from 'react-router-dom'
import { UserContext } from '../../store/UserContext'
import FriendMessage from './ChatBox/UI/FriendMessage'
import UserMessage from './ChatBox/UI/UserMessage'
import {Loader} from '../../Components/Loader'
import { BrowserContext } from '../../store/BrowserContext'
import { TranslationContext } from '../../store/TranslationContext'
import { webSocketDomain } from '../../utils'

const Messages = ({conversationsWidth}) => {
    let bigScreen = window.innerWidth >= 750
    /////////////////////////

    const myComponentRef = useRef();
    const {friendId} = useParams()
    const [loadingSendMessage, setloadingSendMessage] =useState(false)
    const [text, setText] = useState('')
    const {userData} = useContext(UserContext)
    const unsentCount = useRef(0)
    const [friendData, setFriendData] = useState(null) 

    /////////////////
    const isNext = useRef(false)
    const [messagesLoading, setMessagesLoading] = useState(true)
    const [errorLoadingMessages , setErrorLoadingMessages] = useState(null)
    const [messages, setMessages] = useState([])

    const messagesRef = useRef()
    messagesRef.current = messages
    const sendMessage =()=>{
        setloadingSendMessage(true)
        setText('')
        setMessages(prevState=>[{id:'toBeDeleted', text:text, sent_at : 'Sending', state: 'sending', sender : userData.id}, ...prevState])
        setTimeout(()=>{document.getElementById('chatbox').scrollTop = 0}, 0)
        axios.post('/api/messaging/send-message/',
        {
            friend_id: friendId,
            text : text,
        },
        {
            headers : {
              "Content-Type": "multipart/form-data",
              Authorization: `Bearer ${userData.token}`
            }
        }).then(response=>{
            if ( !messagesRef.current.find(m=>m.id == response.data.id) ) {
                setMessages(prev=>[response.data, ...prev.filter(message=>message.id !='toBeDeleted')])
            }
            setloadingSendMessage(false)
        }).catch(error=>{
            setloadingSendMessage(false)
            setMessages(prevState=>{
                unsentCount.current +=1
                prevState = prevState.filter(message => message.state !== 'sending')
                return [{id:'NotSent' + unsentCount.current , text:text, sent_at : 'Not sent', state: 'notSent', sender : userData.id}, ...prevState]
            })
        })
    }
    const getMessages =(seenMessages=null)=>{
        axios.post(`/api/messaging/get-messages/`,
            {'seen-messages' : JSON.stringify(seenMessages == null ? messages.map(message=> Number(message.id) && message.id) : []),
            'friend-id' : friendId},
            {
            headers : {
                "Content-Type": "multipart/form-data",
                'Authorization' : `Bearer ${userData.token}`
            }
        })
        .then((response)=>{
            if (response.data[2]) setFriendData(response.data[2])
            setMessagesLoading(false)
            setNewMessagesLoading(false)
            isNext.current = response.data[1]
            setMessages(prevState=>[...prevState, ...response.data[0]])
        })
        .catch((error)=>{
            setMessagesLoading(false)
            setNewMessagesLoading(false)
            setErrorLoadingMessages(error) 
            document.getElementById('Header').removeAttribute('hidden')
        })
    }
    const location = useLocation()
    useEffect(()=>{
        setFriendData(null)
        isNext.current= false
        setMessagesLoading(true)
        setMessages([])
        getMessages([])
    },[location])
    const [newMessagesLoading, setNewMessagesLoading] = useState(false)
    const getPrevMessages =()=>{
        setNewMessagesLoading(true)
        getMessages()
    }

    //////////////////////////
    //

    useEffect(() => {
        if (userData.id){
            try{
                const socket = new WebSocket(`ws://${webSocketDomain}/chat-room/?token=${userData.token}&friend-id=${friendId}`);
                socket.onmessage = (event) => {
                    const data = JSON.parse(event.data)
                    if (data.type === 'message-update'){
                        if ( !messagesRef.current.find(m=>m.id == data.message.id) ){
                            setMessages(prev=>[data.message, ...prev.filter(message=>message.id !='toBeDeleted')])
                        }
                        setloadingSendMessage(false)
                        if (document.getElementById('chatbox').scrollTop > -66 ){
                            setTimeout(()=>{document.getElementById('chatbox').scrollTop =  0}, 0)
                        }
                    }
                }
                return () => {
                    socket.close();
                };
            }catch{
                console.log(`wss://${ webSocketDomain }/chat-room/?token=${userData.token}&friend-id=${friendId}` + ' Not connected')
            }
        }
    }, [friendId])
    useEffect(() => {
        window.activeChatBoxFriendId = friendId
        return ()=> window.activeChatBoxFriendId = null
    }, [friendId])
    const {Left, S, E} = useContext(BrowserContext)
    const {translate} = useContext(TranslationContext)
    
  return (
        <div className={`bg-white d-flex flex-column vh-100 flex-grow-1 z-index-1`}>
            <div className={`fixed-top bg-white ms-conversation`} style={{marginInlineStart: conversationsWidth, width: window.innerWidth - conversationsWidth, maxWidth: window.innerWidth}}>
                <div className={`gap-2 d-flex align-items-center my-1 p${S}-3 cursor-pointer`}>
                    { !bigScreen && <Link to='/messages/' className='btn btn-outline-success border-0'><i className={`fa-solid fa-chevron-${Left}`}></i></Link>}
                    <Link to={ friendData ? `/@${ friendData.friend_username }/` :'' }>
                        {!friendData ?
                            <>
                                <img width={40} height={40} className='bg-secondary rounded rounded-circle mt-1' ></img>
                                <img width={150} height={20} className='bg-secondary rounded mx-2' ></img>
                            </>:
                            <>
                                <Avatar src={friendData.friend_image} />
                                <strong>{ friendData.friend_name.text ||friendData.friend_name} {friendData.friend_name.is_verified && <img width='16' src='/static/others/verified.png'/>}</strong>
                            </>
                        }
                    </Link>
                </div>
                <hr className='m-0'/>
            </div>
            { messagesLoading ?  
                <div className='d-flex align-items-center justify-content-center vh-100'><Loader className='color-theme' diam='100'/></div>:
                    <div style={{position: 'fixed', bottom: 54, height: 'calc(100% - 103px)', width: `calc( 100% - ${conversationsWidth}px)`}} >
                        <div className='overflow-auto d-flex flex-column-reverse flex-grow-1 fixed-middle h-100 mw-100' style={{padding: `0 8px 8px 8px`}}  id='chatbox' ref={myComponentRef}>
                                <div id='flag'/>
                                {messages.map(message=>{
                                    return message.sender === userData.id ? <UserMessage lastMessageId={messages[0].id} key={message.id} message={message} /> : <FriendMessage lastMessageId={messages[0].id}  key={message.id} message={message}/>
                                })}
                                {isNext.current &&
                                    <button disabled={newMessagesLoading} className={`${newMessagesLoading ? '' : 'color-theme '} text-center p-3 h4`} onClick={getPrevMessages}>
                                        {translate('Show older')}
                                    </button>
                                }
                        </div>
                    </div>
            }
            <div className={`d-flex gap-1 bg-white col-12 border-top fixed-bottom px-1`} style={{marginInlineStart: conversationsWidth, width: window.innerWidth - conversationsWidth, maxWidth: window.innerWidth}}>
                <AutoResizeTextarea value={text} onChange={(e)=>{setText(e.target.value)}} rows='1' className='flex-grow-1 h-fit-content my-1 border rounded p-2' maxLength={255} style={{resize:'none', outline: 'none'}}></AutoResizeTextarea>
                <button disabled={loadingSendMessage || !text.trim()} onClick={sendMessage} className={`btn btn-outline-success px-2 m${E}-1 rounded-1 my-1 border-0 my-2 h-fit-content align-self-end ${ Left != 'left' && 'fa-flip-horizontal' }`}><i className="fa-regular fa-paper-plane fa-xl"></i></button>
            </div>
        </div>  
  )
}

export default Messages