import React, { useContext, useEffect, useRef, useState } from 'react'
import Conversations from './Conversations'
import Messages from './Messages'
import { UserContext } from '../../store/UserContext'
import { useParams } from 'react-router-dom'
import { BrowserContext } from '../../store/BrowserContext'
import { TranslationContext } from '../../store/TranslationContext'


const ChatBox = () => {
    
    useEffect(()=>{
        document.getElementById('Header') && document.getElementById('Header').setAttribute('hidden', '')
        document.getElementById('main').style.setProperty('padding', '0')
        document.getElementById('main').style.setProperty('margin-top', '0')
        return ()=>{
            document.getElementById('Header') && document.getElementById('Header').removeAttribute('hidden')
            document.getElementById('main').style.removeProperty('padding')
            document.getElementById('main').style.removeProperty('margin-top')
        }
    }, [])  

    //////////////////////////
    //
    const [coponentReloader, setComponentReloder] = useState(false)
    const conversationsWidth = useRef(0)
    let bigScreen = window.innerWidth >= 750
    window.conversationsActive = bigScreen
    useEffect(()=>{
        const handleResize=()=>{
            if (window.innerWidth < 750) conversationsWidth.current = 0
            else if (window.innerWidth > 1250) conversationsWidth.current = 500
            else conversationsWidth.current = window.innerWidth * 0.4
            setComponentReloder(prev=>!prev)
        }
        handleResize()
        window.addEventListener('resize', handleResize);
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, [])
    const {conversations, setConversations} = useContext(UserContext)
    const {friendId} = useParams() 

    const {language} = useContext(BrowserContext).browserData
    const {setTitle, setDescription} = useContext(BrowserContext)
    const {translate}=useContext(TranslationContext)
    useEffect(()=>{
        setTitle(translate('ChatBox') + ` | ${translate('Ch7al Machya')}`) // Translate this
        setDescription(translate('description'))
    },[])
    useEffect(()=>{
        if (conversations){
            const currentConveresation = conversations.find(conversation=> conversation.friend_id == friendId)
            if (currentConveresation){
                if (currentConveresation.is_seen == false){
                    setConversations(prev=>{
                        prev.forEach(conversation=>{
                            if (conversation.friend_id == friendId) conversation.is_seen = true
                            return conversation
                        })
                        return prev
                    })
                }
            }
            
        }
            
    }, [conversations])
    
    return (
        <div style={{ marginInlineStart :conversationsWidth.current}} className='d-flex'>
            {bigScreen &&
                <div className={`fixed-top bg-white h-100 overflow-y-auto border-${language == 'ar' ? 'start' : 'end'}`} style={{width: conversationsWidth.current}}>
                    <Conversations  fetchConversations={false}/>
                </div>
            }
            <Messages conversationsWidth={conversationsWidth.current} /> 
        </div>
    
    )
}
export default ChatBox